import Box from '@mui/material/Box'
import Typography from '@mui/material/Typography'

import React, { useContext } from 'react'
import { DateTime } from 'luxon'
import { AuthContext } from '../../context/general'
import withStyles from '@mui/styles/withStyles';


import { grey, red } from '@mui/material/colors'
import Button from '@mui/material/Button'
import { styled } from '@mui/material/styles'








//{ data: event, position, cellWidth, cellHeight, innerSpace }
const VignetteCalendar = ({ data}) => {

    const { authState, authActions } = useContext(AuthContext)
    const dayString = data.startDate.setLocale('fr-FR').toLocaleString(DateTime.DATE_MED)
    const endDayString = data.endDate.setLocale('fr-FR').toLocaleString(DateTime.DATE_MED)
    let [day, month, year] = dayString.split(' ')
    let [endDay, endMonth] = endDayString.split(' ')
    month = month.replace('.', '')
    endMonth = endMonth.replace('.', '')






    const handleClick = (event) => {
        if (authState.filtreDate?.startDate === data.startDate)
            authActions.setFiltreDate(null)
        else {

            authActions.resetTopic({
                index: 888 ,                    //speciial code for reset to null
                filterItem: 'date'
            })

            authActions.setFiltreDate({
                startDate: data.startDate,
                endDate: data.startDate.plus({ days: 6 })
            })



        }

    }

    // @TOTO DEBUG
    // NEED TO CHANGE STYLE FOR MOBILE USERS



    return (<Box width='100%' height='100%' >
        <DateButton onClick={handleClick} selected={data.startDate === authState.filtreDate?.startDate}>
            <Box display="flex" flexDirection="column" margin="5px 15px 5px 15px">


                <Box textAlign="center">
                    <Typography style={{ fontSize: '0.7rem', fontWeight: 'bold' }}>
                        {day}{" "}{month}{" - "}{endDay}{" "}{endMonth}
                    </Typography>

                </Box>
                <Box textAlign="center">
                    <Typography style={{ fontSize: '0.7rem' }}>
                        {year}
                    </Typography>


                </Box>
            </Box>
        </DateButton>

    </Box>)
}
export default VignetteCalendar

const DateButton  = styled(Button)({
    textTransform: 'none',
        backgroundColor: "white",
        zIndex: 2,
        color: 'black',
        borderRadius: '10px',
        fontSize: '0.7rem',

        '&:hover': {
            backgroundColor: grey[200]
        }
    
})






















