// BEGIN CUSTOM
const moreIncrement = 15
// END CUSTOM


const eventListenerQuery = `
subscription eventModifiedListener {
    updatedEvent{
          attendingNb
        id
    }
  }`

const getEvents = `
query MyQuery {
    listEvents(filter: {id: {in: {{ID_LIST}}}}) {
      items {
        id
        nbReserved
        attendingNb
        nbMax
      }
    }
  }`



const getFormatSortCode = (event) => {

    const format = event.format

    const index = eventFormat.indexOf(format)
    return Math.pow(2, 15 - index)

}

Amplify.configure(awsconfig)




const Calendar = ({ data }) => {

    const [isMounted, setIsMounted] = useState(false)

    useEffect(() => {

        setIsMounted(true)
        const ids = upStreamevents.map(event => event.fields.eventId)
        authActions.setEvents(upStreamevents)
        const eventsQuery = getEvents.replace('{{ID_LIST}}', JSON.stringify(ids))


        API.graphql(graphqlOperation(eventsQuery))
            .then(({ data }) => {
                const events = data.listEvents.items
                authActions.updateEvents(events)
            })
            .catch(err => console.log(err))



        API.graphql(graphqlOperation(eventListenerQuery))
            .subscribe({
                next: (data) => {
                    const event = data.value.data.updatedEvent
                    authActions.updateEvents([event])

                },
                error: error => console.warn(error)
            });

    }, [])




    const [calendarStartDate, setCalendarStartDate] = useState(null)
    const { authState, authActions } = React.useContext(AuthContext)
    const classes = useStyles()
    const [showNb, setShowNb] = useState(moreIncrement)
    const [hashFiltres, setHashFiltres] = useState(null)

    React.useEffect(() => {


        let computedHash = null
        if (authState.filtres)
            computedHash = hash(authState.filtres)
        if (computedHash !== hashFiltres) {
            setHashFiltres(computedHash)
            setShowNb(moreIncrement)

        }


    }, [authState])

    if (!data)
        return null

    const filterButtonsFrameWorkSorted = filterButtonsFrameWork.sort((a, b) => a.index - b.index)


    const catégorieSortKey = authState.filtres.catégorie ? authState.filtres.catégorie : (Math.pow(2, 16) - 1)
    const formatSortKey = authState.filtres.format ? authState.filtres.format : (Math.pow(2, 16) - 1)
    const startDate = authState?.filtreDate?.startDate
    const endDate = authState?.filtreDate?.endDate

    const now = DateTime.local()

    const upStreamevents = data.events.edges.map(({ node }) => node)
        .filter(event => event.state === 'published')
        .filter(event => now <= getDate(event.date).luxonDate)


    const eventsMid = filterEvents(
        data.events.edges
        .map(({ node }) => node)
        .filter(event => event.state === 'published')
        
        , catégorieSortKey, calendarStartDate, startDate)
    const eventsRef = filterEventsOnCategory(data.events.edges.map(({ node }) => node), catégorieSortKey)

    // SHOULD BE REMOVED
    //     .filter(event => event.children)
    //     .filter(event => event.children.find(child => child.original))
    //     .filter(event => event.children.find(child => child.state === 'published'))
    //     .sort((a, b) => a.date - b.date)
    //     .filter(event => (event.categoriesCode & catégorieSortKey))
    //     .filter(event => getFormatSortCode(event) & formatSortKey)
    //     .filter(event => {
    //         if (!startDate)
    //             return true
    //         else if ((getDate(event.date).luxonDate >= startDate) && (getDate(event.date).luxonDate <= endDate))
    //             return true
    //         else return false
    //     })



    // const eventsMid = calendarStartDate ? upStreamevents.filter(event => calendarStartDate <= getDate(event.date).luxonDate) : upStreamevents
    // END COMMENTS

    const events = eventsMid.slice(0, showNb)





    return (
        <Box marginTop="20px" width="100%" id="calendarItem">
            <Box display="flex" flexDirection="row">
                <Typography variant="h1">
                    Calendrier
                </Typography>
                <Box width="25px" height="25px" marginLeft="10px" position="relative" bottom="5px">
                    <Icon />
                </Box>
            </Box>

            <Box sx={sxStyleBis.filterButtonContainer} >
                <Box flex={6} display="flex" flexDirection="row" width="380px" justifyContent="start" >
                    {filterButtonsFrameWorkSorted.map(filterButtonItem => <FilterButton filterItem={filterButtonItem.filterItem} key={filterButtonItem.position} index={filterButtonItem.position} label={filterButtonItem.label} />)}

                </Box>
            </Box>
            <CalendarCarrousel eventsMid={eventsRef}  setCalendarStartDate={setCalendarStartDate}/>



            <Box width='calc(100vw-200px)' marginTop='50px'>
                {events
                    .map((event, index) =>  
                   <SingleEvent isMounted={isMounted} index={index} key={'event-' + index} event={event} calendarStartDate={calendarStartDate} />
                 )}
            </Box>
            {(showNb < eventsMid.length) && <Box padding="10px" textAlign="center"><MoreButton onClick={() => setShowNb(showNb + moreIncrement)}>Afficher plus</MoreButton></Box>}
          



        </Box>
    )
}

export default Calendar

const SingleEvent = ({ event, index, isMounted,eventsMid }) => {
    const classes = useStyles()
    const boxRef = useRef(null)



    const { authState } = React.useContext(AuthContext)

    const updatedEvent = authState.events?.find(loopEvent => loopEvent.fields.eventId === event.fields.eventId)



    const children = event.children

    const smallImageUrlWebp = children.find(image => (image.original && image.original.src.includes('L') && image.original.src.includes('300') && image.original.src.includes('jpg')))?.original.src
    const largeImageUrlWebp = children.find(image => (image.original && image.original.src.includes('L') && image.original.src.includes('800') && image.original.src.includes('jpg')))?.original.src


    const smallImageUrl = children.find(image => (image.original && image.original.src.includes('S')))?.original.src
    const largeImageUrl = children.find(image => (image.original && image.original.src.includes('L')))?.original.src

    if (!largeImageUrl && !smallImageUrl)
        return null

    const eventDateString = event.date ? getDate(event.date).date.split(' ') : null
    const dayMonth = eventDateString ? eventDateString[0] + ' ' + eventDateString[1].replace('.', '') : null
    const time = event.date ? getDate(event.date).time : null

    const nbMax = !updatedEvent ? parseInt(event.nbMax) : parseInt(updatedEvent.nbMax)
    const attendingNb = !updatedEvent ? parseInt(event.attendingNb) : parseInt(updatedEvent.attendingNb)
    const nbReserved = !updatedEvent ? parseInt(event.nbReserved) : parseInt(updatedEvent.nbReserved)
    const remainingNb = Math.max(nbMax - (attendingNb ? attendingNb : 0) - (nbReserved ? nbReserved : 0), 0)


    let format = 'Webinar'
    if (nbMax < 50)
        format = 'Classe virtuelle'
    if (nbMax < 25)
        format = 'Atelier'



    const formateur = event.children.find(child => child.nom)

    if (!formateur)
        return null

    const onClick = () => {
        navigate(event.fields.slug)
    }




    const color = randomColor({ luminosity: 'light', hue: 'blue' })


    return (
        // <div data-aos="fade-down" data-aos-delay="100" >
            <Box onClick={onClick} display="flex" flexDirection="row" alignContent="center" padding='10px' justifyItems="stretch">
            <Box sx={sxStyleBis.calendarLeftContainer} ref={boxRef}>
                {isMounted && <Suspense fallback={
                    <Box height={boxRef?.current?.offsetHeight} color={color} bgcolor={color} display="flex" alignContent="center" alignItems="center">
                        @
                    </Box>}>


                    <LazyLoadImage
                        threshold={500}
                        className={classes.calendarImage}
                        alt={formateur.nom}
                        src={smallImageUrlWebp || largeImageUrl} // use normal <img> attributes as props
                    />
                </Suspense>}
                <Box sx={sxStyleBis.descriptionContainer} >
                    <Box >
                        <Typography style={{ fontSize: '0.6rem', fontWeight: 'bold', lineHeight: '0.6rem' }}>
                            {formateur.prenom}{' '}{formateur.nom && formateur.nom?.toUpperCase()}
                        </Typography>

                    </Box>
                    <Box>
                        <Typography style={{ fontSize: '0.5rem', color: grey[700], lineHeight: '1.0rem' }}>
                            {format}
                        </Typography>
                    </Box>
                    <Box>
                        <Typography style={{ fontSize: '0.9rem', color: grey[900], lineHeight: '1.1rem' }}>
                            {event.title1}
                        </Typography>
                    </Box>
                    <Box>
                        <Typography style={{ fontSize: '0.8rem', color: grey[500], lineHeight: '0.8rem' }}>
                            {event.title2}
                        </Typography>
                    </Box>

                </Box>
            </Box>

            <Box sx={sxStyleBis.calendarRightContainer} >




                <Box sx={sxStyleBis.calendarRemainingPlaces}>
                    <Typography style={{ fontSize: '0.8rem', fontWeight: 'bold', textAlign: "center" }}>
                        {remainingNb}
                    </Typography>
                    <Typography style={{ fontSize: '0.8rem', textAlign: "center" }}>
                        {"place" + ((remainingNb > 1) ? "s" : "") + " restante" + ((remainingNb > 1) ? "s" : "")}
                    </Typography>

                </Box>

                <Box flex={1} display='flex' flexDirection='column' justifyContent='center' alignContent='center' alignItems='center' >

                    <Typography style={{ textAlign: "center", fontSize: '0.8rem', fontWeight: 'bold', width: "100%" }}>
                        {dayMonth}
                    </Typography>
                    <Typography style={{ fontSize: '0.8rem' }}>
                        {time.replace(':', 'h')}
                    </Typography>
                </Box>
                <Box sx={sxStyleBis.calendarButtonContainer}>
                    <CalendarButton onClick={onClick}>
                        Participer
                    </CalendarButton>
                </Box>
            </Box>

       


    </Box>
    // </div>
    )
}



const FilterButton = (props) => {

    const { label, index, state, filterItem } = props

 



    const { authState } = React.useContext(AuthContext)

    const selected = authState?.filtres[filterItem]

    const [open, setOpen] = React.useState(false)
    const anchorRef = React.useRef(null)



    const handleToggle = () => {
        setOpen((prevOpen) => !prevOpen);
    };



    const style = { fontSize: '1rem' }

    return (<Box padding='5px'>
        <BottomFilterButton onClick={handleToggle} ref={anchorRef} selected={Boolean(selected)}>

            {label} {state ? <KeyboardArrowUpIcon style={style} /> : <KeyboardArrowDownIcon style={style} />}
        </BottomFilterButton>
        <FilterMenu anchorRef={anchorRef} filterItem={filterItem} open={open} setOpen={setOpen} index={index} />

    </Box>)
}

const FilterMenu = ({ anchorRef, open, setOpen, index, filterItem }) => {


    const filterButtons = filterButtonsFrameWork.find(button => button.position === index)

    const menuitems = filterButtons.choices

    const { authActions } = React.useContext(AuthContext)


    const handleClick = (index) => (event) => {

        if (filterItem === 'date')                     //handle click on Date filter
        {


            const today = DateTime.now().setLocale('fr-FR').startOf('day')
            const weekday = today.weekday

            if (index === 0)                                   //cette semaine
                authActions.setFiltreDate({
                    startDate: today,
                    endDate: today.plus({ days: 7 - weekday })
                })

            if (index === 1)                                   //ce week-end
                authActions.setFiltreDate({
                    startDate: today.plus({ days: 6 - weekday }),
                    endDate: today.plus({ days: 7 - weekday })
                })

            if (index === 2)                                   //ce week-end
                authActions.setFiltreDate({
                    startDate: today.plus({ days: 8 - weekday }),
                    endDate: today.plus({ days: 14 - weekday })
                })

            if (index === 3)                                   //ce week-end
                authActions.setFiltreDate({
                    startDate: today.plus({ days: 13 - weekday }),
                    endDate: today.plus({ days: 14 - weekday })
                })



            authActions.resetTopic({
                index: index,
                filterItem: filterItem
            })



        }
        else {

        

            authActions.toggleTopic({
                index: index,
                filterItem: filterItem
            })

        }



        scrollTo('#calendarItem')



    }

    function handleListKeyDown(event) {
        if (event.key === 'Tab') {
            event.preventDefault();
            setOpen(false);
        }
    }

    const handleClose = () => {
        setOpen(false)
    }


    return (<Popper style={{ zIndex: 999 }} placement="right" open={open} anchorEl={anchorRef.current} role={undefined} transition disablePortal>
        {({ TransitionProps, placement }) => (
            <Grow
                {...TransitionProps}
                style={{ transformOrigin: 'center top' }}
            >
                <Paper>
                    <ClickAwayListener onClickAway={handleClose}>
                        <MenuList MenuListProps={{ onMouseLeave: handleClose }} autoFocusItem={open} id={"menu-list-grow" + index} onKeyDown={handleListKeyDown}

                        >
                            {menuitems.map((label, index) => <LocalMenuItem filterItem={filterItem} key={index} index={index} onClick={handleClick(index)} label={label} />)}

                        </MenuList>
                    </ClickAwayListener>
                </Paper>
            </Grow>
        )}
    </Popper>)
}

const LocalMenuItem = ({ onClick, label, index, filterItem }) => {


    const { authState, authActions } = React.useContext(AuthContext)




    const show = authState.filtres[filterItem] & Math.pow(2, 15 - index)

    return (
        <MenuItem style={{ borderLeft: show ? '5px solid #50C0C0' : '5px solid transparent' }} onClick={onClick}  >{label}</MenuItem>
    )
}

