
import React, { useState, useEffect } from 'react'

import Box from '@mui/material/Box'


import { DateTime } from 'luxon'
import getDate from '../../helpers/dates'

import Carousel from '../../components/caroussels/carousel'
import VignetteCalendar from '../../components/caroussels/vignetteCalendar'




const today = DateTime.now().startOf('day')
const offets = Array(120).fill().map((_, i) => i)
const dates = offets.map(offset => today.plus({ days: offset*7 }))
const vignettesDates = dates.map(date=> ({startDate: date, endDate: date.plus({days: 6})}))




const CalendarCarrousel = ({ setCalendarStartDate, eventsMid }) => {

    const [isActive, setIsActive] = useState(false)
    const [isMounted, setIsMounted] = useState(false)

    const eventDates = eventsMid.map(event => getDate(event.date).luxonDate)

    const usefulvignettesDates = vignettesDates
                                .filter(period => {
                                    const found = eventDates.filter(date => date >= period.startDate && date <= period.endDate)
                                    
                                    return found.length > 0
                                })

    


//@TOTO
    useEffect(() => {

  

        if(!isMounted)
        setIsMounted(true)


        setCalendarStartDate(dates[0])

    }, [isMounted, setCalendarStartDate])

    return (
        <Box>
            <Carousel isMounted={isMounted} cellTemplate={VignetteCalendar} cellsData={usefulvignettesDates} carouselConfig={carouselConfig} isActive={isActive} setIsActive={setIsActive} />
        </Box>

    )
}

export default CalendarCarrousel



const carouselConfig = {
    title: "",
    titleMarginBottom: '10px',
    titleBorderBottom: 'none',
    carouselHeight: 150,
    dots: false,
    cellRatio: 0.35,
    breakpoints: {
        200: {
            slidesPerView: 2,
            slidesPerGroup: 2,
            spaceBetween: 10
        },
        800: {
            slidesPerView: 4,
            slidesPerGroup: 4,
            spaceBetween: 10
        },

            
      
        1100: {
            slidesPerView: 5,
            slidesPerGroup: 5,
            spaceBetween: 10
        },
        // when window width is >= 480px
        1220: {
            slidesPerView: 5,
            slidesPerGroup: 5,
            spaceBetween: 10
        },
        // when window width is >= 640px
        1500: {
            slidesPerView: 6,
            slidesPerGroup: 6,
            spaceBetween: 10
        }
    }
    
}